@use '../base/_mixins.scss' as m;
@use '../base/_variables.scss' as v;

.contacto {
    text-align: center;
    @include m.flex(none, none);
    flex-direction: column;
    margin-bottom: 10rem;
    form {
        @include m.flex(center, none);
        flex-direction: column;
        font-size: 2rem;
       
        input,
        textarea {
            width: 45rem;
            padding: 2rem;
            margin-top: 1rem;
            border-radius: 0.5rem;
            border: solid black 0.1rem;
        }

        .email {
            height: 3rem;
        }

        .texto {
            height: 20rem;
            line-height: 2.5rem;
        }

        button {
            width: 10rem;
            padding: 1rem;
            background-color: v.$n;
            color: v.$b;
            margin-top: 1rem;
        }
    }
    @include m.telefono{
        form{
            input,
            textarea{
                width: 30rem;
            }
            
        }
        p{
            margin: 2rem auto 2rem auto;
            width: 90%;
        }
        
    
    }
}