@use "variables" as v;

html {
    font-size: 62.5%; // 1rem = 10px
    box-sizing: border-box;
}

*,
*::after,
*::before {
    box-sizing: inherit; //El padding no represente un problema en el documento
}

body {
    font-family: v.$secundaria;
    font-size: 1.6rem; //16px
     background: rgb(253, 252, 251);
    background: linear-gradient(95deg, rgba(253, 252, 251, 1) 25%, rgba(227, 210, 196, 1) 52%);
}


p {
    font-size: 2rem;
    color: v.$n;
    line-height: 1.5;
}

.contenedor {
    width: 90%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1 {
    font-weight: 700;
    font-family: v.$principal;
}

h2,
h3 {
    font-weight: 400;
    font-family: v.$principal;
    margin: 0px;
}

h1 {
    font-size: 6rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
    height: auto;

}

.bold {
    font-weight: 700;
}