@use '../base/_mixins.scss' as m;
@use '../base/_variables.scss' as v;

header {
    @include m.flex(center, space-between);
    background-color: rgba($color: v.$b, $alpha: .9);
    position: sticky;
    top: 0;
    padding: 1rem 0;

    &:hover {
        background-color: v.$b;
    }

    img {
        width: 3rem;
        margin-top: 5px;
    }

    div {
        @include m.flex(center, space-evenly);
        width: 25%;

        img {
            width: 4rem;
            height: auto
        }

    }

    nav {
        @include m.flex(center, space-evenly);
        width: 40%;

        .imagenes {
            @include m.flex(center, space-evenly);
            width: 45%;
        }

        .enlaces {
            @include m.flex(center, space-evenly);
            width: 45%;
        }

        a {
            color: v.$n;
            font-weight: 600;
        }

        img {
            margin-top: 5px;
        }

        .hover:hover {
            border-bottom: solid v.$n 1px;
            padding-bottom: 2px;
            margin-top: 3px;
        }

        button {
            padding: 1rem;
            background-color: v.$n;
            color: v.$b;
            border-radius: .5rem;
            font-size: 1.75rem;
            cursor: pointer;
            font-family: v.$principal;
            font-weight: 700;
            a{
                color: v.$b;
            }
            &:hover {
                background-color: v.$g;
            }
        }
    }

    @include m.desktopXL {

        position: static;
        top: 1px;
        background-color: v.$b;
        flex-direction: column;

        div {
            width: 35%;
        }

        nav {
            flex-direction: column;
            margin-top: 2rem;
            gap: 2rem;
            width: 70%;

            .hover:hover {
                border-bottom: 0px;
                margin: 0;
                padding: 0;
            }

        }


    }

    @include m.tablet {
        div {
            width: 50%;
        }

        nav {
            width: 90%;
        }
        .imagenes {
            margin-top: 1.5rem;
            width: 100%;
        }
    }

    @include m.telefono {
        div {
            width: 90%;
        }

        nav {
            width: 100%;

            .enlaces {

                width: 100%;
            }

            .imagenes {
                margin-top: 1.5rem;
                width: 100%;
            }
        }

    }
}