@use "variables" as v;

@mixin telefono{
    @media (max-width: v.$telefono){
        @content; //Soporta bloques de codigo
    }
}

@mixin tablet{
    @media (max-width: v.$tablet){
        @content; //Soporta bloques de codigo
    }
}


@mixin desktop{
    @media (max-width: v.$desktop){
        @content; //Soporta bloques de codigo
    }
}

@mixin desktopXL{
    @media (max-width: v.$desktopXL){
        @content; //Soporta bloques de codigo
    }
}

@mixin flex($AI, $JC){
    display:flex;
    justify-content: $JC;
    align-items: $AI;
}