html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  background: rgb(253, 252, 251);
  background: linear-gradient(95deg, rgb(253, 252, 251) 25%, rgb(227, 210, 196) 52%);
}

p {
  font-size: 2rem;
  color: black;
  line-height: 1.5;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1 {
  font-weight: 700;
  font-family: "Montserrat Alternates", sans-serif;
}

h2,
h3 {
  font-weight: 400;
  font-family: "Montserrat Alternates", sans-serif;
  margin: 0px;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  height: auto;
}

.bold {
  font-weight: 700;
}

.home {
  text-align: center;
}
.home p {
  letter-spacing: 2px;
}

.imagenes_home {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.imagenes_home .abajo {
  margin-top: 10rem;
}
.imagenes_home img {
  margin: auto;
  width: 85%;
}

@media (max-width: 992px) {
  .contenedor {
    width: 90%;
  }
}
@media (max-width: 480px) {
  .contenedor {
    width: 100%;
  }
}
.capacidades {
  text-align: center;
}
.capacidades h2 {
  margin-bottom: 8rem;
}
.capacidades > div {
  display: flex;
  justify-content: center;
  align-items: none;
}
.capacidades > div div {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
}
.capacidades img {
  width: 90%;
  border-radius: 3rem;
}
.capacidades h3 {
  margin: 3rem 0;
}
.capacidades p {
  width: 90%;
  margin: 0px auto 5rem auto;
  height: 6rem;
  text-align: center;
}
.capacidades span {
  font-weight: 700;
}
@media (max-width: 992px) {
  .capacidades > div {
    flex-direction: column;
  }
}

footer {
  text-align: center;
  height: 35rem;
  width: 100%;
  background-color: black;
}
footer img {
  padding-top: 5rem;
  width: 3rem;
}
footer p {
  font-weight: 300;
  color: white;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.9);
  position: sticky;
  top: 0;
  padding: 1rem 0;
}
header:hover {
  background-color: white;
}
header img {
  width: 3rem;
  margin-top: 5px;
}
header div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 25%;
}
header div img {
  width: 4rem;
  height: auto;
}
header nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 40%;
}
header nav .imagenes {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 45%;
}
header nav .enlaces {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 45%;
}
header nav a {
  color: black;
  font-weight: 600;
}
header nav img {
  margin-top: 5px;
}
header nav .hover:hover {
  border-bottom: solid black 1px;
  padding-bottom: 2px;
  margin-top: 3px;
}
header nav button {
  padding: 1rem;
  background-color: black;
  color: white;
  border-radius: 0.5rem;
  font-size: 1.75rem;
  cursor: pointer;
  font-family: "Montserrat Alternates", sans-serif;
  font-weight: 700;
}
header nav button a {
  color: white;
}
header nav button:hover {
  background-color: rgb(80, 80, 80);
}
@media (max-width: 1200px) {
  header {
    position: static;
    top: 1px;
    background-color: white;
    flex-direction: column;
  }
  header div {
    width: 35%;
  }
  header nav {
    flex-direction: column;
    margin-top: 2rem;
    gap: 2rem;
    width: 70%;
  }
  header nav .hover:hover {
    border-bottom: 0px;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 768px) {
  header div {
    width: 50%;
  }
  header nav {
    width: 90%;
  }
  header .imagenes {
    margin-top: 1.5rem;
    width: 100%;
  }
}
@media (max-width: 480px) {
  header div {
    width: 90%;
  }
  header nav {
    width: 100%;
  }
  header nav .enlaces {
    width: 100%;
  }
  header nav .imagenes {
    margin-top: 1.5rem;
    width: 100%;
  }
}

.servicio {
  margin: 0 auto;
}
.servicio h2 {
  text-align: center;
}
.servicio div {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8rem;
}
.servicio div picture {
  width: 100%;
}
.servicio div img {
  width: 90%;
}
.servicio div p {
  width: 90%;
  font-size: 2.5rem;
  line-height: 4.5rem;
  text-align: justify;
}
@media (max-width: 768px) {
  .servicio h2 {
    margin-bottom: 5rem;
  }
  .servicio div {
    display: block;
  }
  .servicio div img {
    width: 85%;
    display: block;
    margin: auto;
    margin-bottom: 2rem;
  }
  .servicio div p {
    text-align: justify;
    line-height: 3.5rem;
    width: 75%;
    margin: auto;
  }
}

.contacto {
  text-align: center;
  display: flex;
  justify-content: none;
  align-items: none;
  flex-direction: column;
  margin-bottom: 10rem;
}
.contacto form {
  display: flex;
  justify-content: none;
  align-items: center;
  flex-direction: column;
  font-size: 2rem;
}
.contacto form input,
.contacto form textarea {
  width: 45rem;
  padding: 2rem;
  margin-top: 1rem;
  border-radius: 0.5rem;
  border: solid black 0.1rem;
}
.contacto form .email {
  height: 3rem;
}
.contacto form .texto {
  height: 20rem;
  line-height: 2.5rem;
}
.contacto form button {
  width: 10rem;
  padding: 1rem;
  background-color: black;
  color: white;
  margin-top: 1rem;
}
@media (max-width: 480px) {
  .contacto form input,
.contacto form textarea {
    width: 30rem;
  }
  .contacto p {
    margin: 2rem auto 2rem auto;
    width: 90%;
  }
}