@use '../base/mixins' as m; 
@use '../base/variables' as v;


.home{
    text-align: center;
    
    p{
        letter-spacing: 2px;
    }
  
}


.imagenes_home{
    
    @include m.flex(center, space-evenly);
    .abajo{
        margin-top: 10rem;
    }
    img{
        margin: auto;
        width: 85%; 
    }
  
}
@include m.desktop {
    .contenedor{
        width: 90%;
    }
}
@include m.telefono {
    .contenedor{
        width: 100%;
    }
}