@use '../base/_mixins.scss' as m;
@use '../base/_variables.scss' as v;

.servicio {
    margin:0 auto ;
    h2 {
        text-align: center;
    }

    div {
       
        @include m.flex(center, center);
        margin-top: 8rem;
        picture{
            width: 100%;
        }
        img{
            width: 90%;
        }
        p{
            width: 90%;
            font-size: 2.5rem;
            line-height: 4.5rem;
            text-align: justify;
        }
    }
    @include m.tablet {
        h2{
            margin-bottom: 5rem;
        }
        div{    
            display: block;
           
            img{    
                width:85%;
                display: block;
                margin:auto;
                margin-bottom: 2rem;
            }
            p{
                text-align: justify;
                line-height: 3.5rem;
                width:75%;
                margin:auto;
            }
        }
        
    }
}
