@use '../base/_mixins.scss' as m;
@use '../base/_variables.scss' as v;

.capacidades{
    text-align: center;
    h2{
        margin-bottom: 8rem;
    }
    &>div{
        @include m.flex(none, center);
        div{
            display: flex;
            flex-direction: column;
            
            justify-content: space-evenly;
            width:100%;
        }
    }
    img{  
        width: 90%;
        border-radius: 3rem;
    }
    h3{
        margin: 3rem 0;
    }
    p{
        width: 90%;
        margin: 0px auto 5rem auto;
        height: 6rem;
        text-align: center;

    }
    span{
        font-weight: 700;
    }
    @include m.desktop {
        &>div{
            flex-direction: column;
        }
      
    }
}